<template>
  <div>
    <Header></Header>
    <div class="base-content abc-flex-x-between">
      <Left></Left>
      <div class="add-project">
        <div class="title">
          {{ this.editMark == 1 ? '' : '' }}
        </div>

        <div class="content">
          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div><input v-model="projectName" class="project-name" type="text"></div>
          </div>

          <!---->
          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div class="person-info-par">
              <div class="person-info">
                <select v-for="(item, index) in personList" v-model="personList[index]">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                  <option value="6"></option>
                  <option value="7"></option>
                </select>

              </div>

              <div @click="addPerson" class="add-person abc-img"><img src="../../../assets/img/payroll/addProject/add_icon.png"></div>
              <div v-if="personList.length > 1" @click="removePerson"
                   class="remove-person abc-img"><img src="../../../assets/img/payroll/addProject/remove_icon.png"></div>

            </div>
          </div>

          <!---->
          <div class="time-all">
            <div class="data-detail">
              <div class="list-header abc-flex-x-start">
                <div style="width: 150px;"></div>
                <div></div>
              </div>

              <div class="list-body">

                <div v-for="(item, index) in personList_new" class="body-item abc-flex-x-start">
                  <div style="width: 150px;">{{ item.name || '-' }}</div>
                  <div><input v-model="item.hours" @input="changeHours(item, index)"
                              type="text" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'></div>
                </div>

              </div>

            </div>
          </div>

          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div>{{ incomeAll || '0' }}</div>
          </div>

          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div><input v-model="addIncome" class="income" placeholder=""></div>
          </div>

          <div class="start-time abc-flex-x-start">
            <div class="content-title"></div>
            <div class="abc-flex-x-start">
              <div class="abc-img"><img src="../../../assets/img/payroll/addProject/date.png"></div>
              <div class="start-time-input">
                <input @keyup="startTimeKeyup" :value="startTime_str"
                       type="text" class="startTime" placeholder="">
              </div>

            </div>
          </div>

          <div class="end-time abc-flex-x-start">
            <div class="content-title"></div>
            <div class="abc-flex-x-start">
              <div class="abc-img"><img src="../../../assets/img/payroll/addProject/date.png"></div>
              <div class="end-time-input">
                <input @keyup="endTimeKeyup" :value="endTime_str"
                       type="text" class="endTime" placeholder="" />
              </div>

            </div>
          </div>

          <div class="status abc-flex-x-start">
            <div class="content-title"></div>
            <div class="complete">
              <select v-model="complete">
                <option value="1"></option>
                <option value="2"></option>
              </select>
            </div>
          </div>

        </div>

        <div class="btn-group abc-flex-x-start">
          <div @click="confirm" class="abc-btn"></div>
          <a class="abc-btn" href="./payroll.html"></a>
        </div>

      </div>
    </div>


    <of_dialog :message="message"></of_dialog>
  </div>
</template>

<script>
import moment from 'moment'
import { personMapList, handleData } from './computed/index'
import date_plugin from '@/assets/js/focus-vendor/date' // 
import { incomeMap } from '@/assets/js/static-data/income'
import { getUrl } from '@/assets/js/focus-vendor/getObj'

import Header from '../../common/header.vue'
import Left from '../../common/left.vue'
import of_dialog from '@/views/components/dialog.vue'

import { addProject } from '@/api/addProject'
import { getProject } from '@/api/projectList'

export default {
  name: 'addProject',
  data () {
    return {
      personList_result: [], // ()
      incomeAll: 0, // ,
      message: {}, // 

      personList: ['0'], // id
      startTime: '', // 
      endTime: '', // 
      addIncome: '', // 
      complete: '1', // 12
      projectName: '', // 
      id: '', // editMark2id

      confirmMark: true, // 

      editMark: '1' // 12
    }
  },
  computed: {
    // 
    personList_new () {
      let arr = [] // 
      let incomeAll = 0 // 
      this.personList.map((item, index) => {

        // hours***
        const hours = this.personList_result.length && this.personList_result[index] ? this.personList_result[index].hours : ''

        const obj = {
          id: item,
          name: personMapList(item),
          hours: hours,
          perHoursMoney: incomeMap[item] || ''
        }

        arr.push(obj)

        // 
        const income_hours = hours || 0
        const unit = incomeMap[item] || 0
        incomeAll += unit * income_hours

      })

      this.incomeAll = incomeAll

      return arr
    },

    startTime_str () {
      return this.startTime ? moment(parseInt(this.startTime)).format('YYYY-MM-DD') : ''
    },
    endTime_str () {
      return this.endTime ? moment(parseInt(this.endTime)).format('YYYY-MM-DD') : ''
    }

  },
  created () {

  },
  watch: {

  },
  async mounted () {
    this.datePlugin() // 

    // 
    this.edit()

  },
  methods: {
    // 
    addPerson () {
      // 
      const newList = this.personList.slice()
      newList.push('0')
      this.personList = newList

      // 
      const newList_result = this.personList_result.slice()
      newList_result.push({id: '', name: '', hours: ''}) // .
      this.personList_result = newList_result
    },

    // 
    removePerson () {
      // 
      const newList = this.personList.slice()
      newList.pop()
      this.personList = newList

      // 
      const newList_result = this.personList_result.slice()
      newList_result.pop()
      this.personList_result = newList_result
    },

    // 
    changeHours (item, index) {
//      console.log(item)
//      console.log(index)

      const personListNew = this.personList_new.slice()
      this.personList_result = personListNew
    },

    startTimeKeyup () {
      this.startTime = ''
      $('.startTime').val('')

    },
    endTimeKeyup () {
      this.endTime = ''
      $('.endTime').val('')

    },

    // 
    async confirm () {
      let that = this

      // 
      if (!this.confirmMark) return
      this.confirmMark = false

      const para = {
        projectName: this.projectName, // 
        personList: this.personList_new,  // id
        startTime: this.startTime,    // 
        endTime: this.endTime,        // 
        addIncome: this.addIncome,    // 
        complete: this.complete,      // 12
        edit: this.editMark == 1 ? 1 : 2 // 12
      }

      // id
      if (this.editMark == 2) {
        para.id = this.id
      }

      // 
      console.log(para)
      const validate = this.validate(para)
      if (!validate) {
        this.message = {
          html: '',
          visiable: true // 
        }

        this.confirmMark = true // 

        return false
      }

      try {
        const dataList = await addProject(para)
//        console.log(dataList)

        this.message = {
          html: '',
          btnType: '3',
          callback () {
            setTimeout(() => {
              window.location.href = './payroll.html'
            }, 1500)
          },
          visiable: true // 
        }

      } catch (error) {
        this.message = {
          html: error.message || '',
          callback () {
            that.confirmMark = true // 
          },
          visiable: true // 
        }

      }

    },

    // 
    validate (data) {
      let mark = true

      // ,false
      for (let key in data) {
        if (!data[key]) {
          mark = false
          break
        }
      }

      // 
      const personList = data.personList
      personList.map((item) => {
        if (!item.id || !item.name || !item.hours) {
          mark = false
        }
      })

      return mark
    },

    // 
    datePlugin () {
      const that = this

      date_plugin('.startTime', {
        stopShow: false, // onShowtrue990
        onShow: function () {},
        onChangeDateTime: function (dp, $input) {
//        console.log($input, dp)
          // 
          const startStamp = moment(dp).format('x')
          that.startTime = startStamp
        },
        onChangeMonth: function (dp, $input) {},
//      minDate: startTime,
//      maxDate: endTime,
        scrollMonth: false,
        // scrollTime: false,
        // scrollInput: false
      })

      date_plugin('.endTime', {
        stopShow: false, // onShowtrue990
        onShow: function () {},
        onChangeDateTime: function (dp, $input) {
//        console.log($input, dp)
          // 
          const endStamp = moment(dp).format('x')
          that.endTime = endStamp
        },
        onChangeMonth: function (dp, $input) {},
//      minDate: startTime,
//      maxDate: endTime,
        scrollMonth: false,
        // scrollTime: false,
        // scrollInput: false
      })

    },


    // 
    async edit () {
      const url = window.location.href
      const windowPara = url.indexOf('?') && url.split('?')[1] ? url.split('?')[1] : ''
      const id = getUrl(windowPara).id ? getUrl(windowPara).id : '' // getid

      // id
      if (!id) return

      this.editMark = '2' // 

      try {
        const para = 'id='+id+''
        const dataList = await getProject(para)

        const data = dataList.data || ''
        const dataResult = handleData(data) // 

        // 
        this.projectName = dataResult.projectName // 
        this.personList_result = dataResult.personList
        this.personList = dataResult.personArr // id
        this.startTime = dataResult.startTime // 
        this.endTime = dataResult.endTime // 
        this.addIncome = dataResult.addIncome // 
        this.complete = dataResult.complete // 12
        this.id = dataResult.id || '' // edit=2(id)

      } catch (error) {
        this.message = {
          html: error.message || '',
          visiable: true // 
        }

      }

    }

  },
  components: {
    Header,
    Left,
    of_dialog
  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../../../assets/js/vendor/datetimepicker/jquery.datetimepicker.css';

  .base-content {
    margin: 0 auto;
    width: 1200px;
    min-height: 800px;
    align-items: flex-start;
  }
  .add-project {
    width: 980px;
    min-height: 1000px;

    .title {
      height: 50px;
      width: 980px;
      border-bottom: 1px solid #979797;
      line-height: 50px;
      font-size: 20px;
      text-align: left;
    }

    .content {
      & > div {
        margin-top: 12px;
        text-align: left;

        .content-title {
          width: 150px;
        }

        .person-info-par {
          width: 800px;
          overflow: hidden;

          .person-info {
            float: left;
            overflow: hidden;
            select {
              float: left;
              margin-right: 10px;
              width: 100px;
              height: 30px;
            }
          }

          .add-person {
            float: left;
            cursor: pointer;
            width: 20px;
            height: 20px;
            margin-top: 5px;
            margin-right: 5px;
          }
          .remove-person {
            float: left;
            cursor: pointer;
            margin-top: 6px;
            width: 18px;
            height: 18px;
          }

        }

        .data-detail {
          margin-left: 150px;
          .list-header {
            background: #E1E1E1;
            width: 400px;
            height: 40px;
            padding: 10px 0 10px 10px;
            & > div {
              width: 170px;
              margin-right: 20px;
            }
            .operate {
              margin-right: 0;
            }
          }

          .list-body {
            width: 400px;
            .body-item {
              width: 400px;
              height: 40px;
              padding: 10px 0 10px 10px;
              border-bottom: 1px solid #CACACA;
              & > div {
                width: 170px;
                margin-right: 20px;

                input {
                  width: 80px;
                  height: 30px;
                  background: #f0f0f0;
                  color: #000;
                  padding: 0 5px;
                }
              }
              .operate {
                margin-right: 0;
                & > div {
                  cursor: pointer;
                }
              }
            }
          }
        }

        .start-time-input input, .end-time-input input {
          width: 120px;
          height: 30px;
          border: 1px solid #ccc;
          border-radius: 5px;
          margin-left: 10px;
          padding: 0 5px;
        }

        .complete {
          select {
            width: 100px;
            height: 30px;
          }
        }

        .income, .project-name {
          width: 120px;
          height: 30px;
          background: #f0f0f0;
          color: #000;
          padding: 0 5px;
        }

      }

    }

    .btn-group {
      margin-top: 100px;
      .abc-btn {
        margin-right: 20px;
        color: #fff;
      }
    }




  }

</style>



