
// id
function personMapList (id) {
  const idNew = String(id)
  let personName = ''

  switch (idNew) {
    case ('1') : personName = ''; break;
    case ('2') : personName = ''; break;
    case ('3') : personName = ''; break;
    case ('4') : personName = ''; break;
    case ('5') : personName = ''; break;
    case ('6') : personName = ''; break;
    case ('7') : personName = ''; break;
  }

  return personName
  
}

// 
function handleData (arr = '') {
  const data = arr ? arr[0] : ''

  // person_list
  let arrNew = []
  const person_list = data.person_list ? JSON.parse(data.person_list) : []
  person_list.map((item) => {
    arrNew.push(item.id)
  })

  const dataInfo = {
    projectName: data.project_name || '', // 
    personArr: arrNew,
    personList: data.person_list ? JSON.parse(data.person_list) : [],  // id
    startTime: data.start_time || '',    // 
    endTime: data.end_time || '',        // 
    addIncome: data.add_income || '',    // 
    complete: data.complete,             // 12
    id: data.id || '' // 
  }

  return dataInfo
}




export {
  personMapList,
  handleData

}


